import React, { useState, useEffect } from 'react';
import './WorkPage.scss';
import {useSpring, animated} from 'react-spring'; 
import {JSONLD, Generic} from 'react-structured-data'
import LazyLoad from 'react-lazyload'; 
import $ from 'jquery';
import ReactDOMServer from 'react-dom/server';

import {ButtonDecorate} from '../ButtonDecorate/index';
import {WorkPageGallery} from '../WorkPageGallery/index';
import MassonryGallery from '../MassonryGallery/MassonryGallery';
import LazyPhotoLoad from '../LazyPhotoLoad/LazyPhotoLoad';
import {getToken, getData} from '../../store/actions'; 
import Preloader from '../Preloader/Preloader';

const shuffle = (arr) => {
	let j, temp;
	for(let i = arr.length - 1; i > 0; i--){
		j = Math.floor(Math.random()*(i + 1));
		temp = arr[j];
		arr[j] = arr[i];
		arr[i] = temp;
	}
	return arr;
}

const WorkPage = ({id, language, works, allImages, currentWorkData, setCurrentWorkData, area}) => {
  const [showDetails, setShowDetails] = useState(true);
  const [contentHeight, setContentHeight] = useState(0); 

  useEffect(() => {
    getToken('http://yova.praid.com.ua/api/login')
      .then(data => data.data['api_token'])
      .then(token => {
        getData(`http://yova.praid.com.ua/api/${area}/${id}`, token)
          .then(data => setCurrentWorkData(data))
          .catch(err => console.log(err))
      })
  }, [])

  useEffect(() => {
    if (!currentWorkData) return
    setContentHeight(document.getElementById('contentShow').scrollHeight); 
    setShowDetails(false)
  }, [currentWorkData])

  const currentAllImages = allImages
    .filter(item => (area === 'works' ? item.worksID : item.socialityID) === id)
    .sort((first, second) => first.order - second.order); 

  const showContentAnimation = useSpring({ height: showDetails ? 'auto' : 0, opacity: showDetails ? 1 : 0, visibility: showDetails ? 'visible' : 'hidden' })
 
  const worksForAlsoLike = (arr) => {
    let newWorks = arr.filter(item => item.id != id)
    return shuffle(newWorks)
  }

  if (!currentWorkData) {
    return (
      <Preloader />
    )
  } else {
    return (  
      <section className="work" itemscope itemtype="http://schema.org/CreativeWork" itemProp="isFamilyFriendly">
        <div className="wrapper">
          <LazyPhotoLoad actualClass="work__image" image={currentWorkData[0].mainImage} alt={currentWorkData.title} />
          <h1 className="work__left-mobtitle" itemProp="name">
            {language === 'en' ? currentWorkData[0].title : currentWorkData[1].title}
          </h1>
            <animated.div id="contentShow" className="work__info" style={window.innerWidth < 799 ? showContentAnimation : null}>
              <div className="work__left">
                <table className="work__left-table">
                  <tbody>
                    <tr>
                      <td>{language === 'en' ? 'Location' : 'Розташування'}</td>
                      <td itemProp="contentLocation">{currentWorkData[language === 'en' ? 0 : 1].location}</td>
                    </tr>
                    <tr>
                      <td>{language === 'en' ? 'Function' : 'Функції'}</td>
                      <td itemProp="itemOffered">{currentWorkData[language === 'en' ? 0 : 1].function}</td>
                    </tr>
                    <tr>
                      <td>{language === 'en' ? 'Area' : 'Площа'}</td>
                      <td itemProp='materialExtent'>{currentWorkData[language === 'en' ? 0 : 1].area}</td>
                    </tr>
                    <tr>
                      <td>{language === 'en' ? 'Status' : 'Статус'}</td>
                      <td itemProp="creativeWorkStatus">{currentWorkData[language === 'en' ? 0 : 1].status}</td>
                    </tr>
                    <tr>
                      <td>{language === 'en' ? 'Designer' : 'Дизайнер'}</td>
                      <td itemProp="author">{currentWorkData[language === 'en' ? 0 : 1].designer}</td>
                    </tr>
                    <tr>
                      <td>{language === 'en' ? 'Art pattern' : 'Художній паттерн'}</td>
                      <td itemProp="genre">{currentWorkData[language === 'en' ? 0 : 1].art_pattern}</td>
                    </tr>
                    <tr>
                      <td>{language === 'en' ? 'Studio' : 'Студія'}</td>
                      <td itemProp="locationCreated">{currentWorkData[language === 'en' ? 0 : 1].studio}</td>
                    </tr>
                    <tr>
                      <td>{language === 'en' ? 'Photographer' : 'Фотограф'}</td>
                      <td itemProp="contributor">{currentWorkData[language === 'en' ? 0 : 1].photographer}</td>
                    </tr>
                  </tbody>
                </table>
                <LazyLoad height={$(window).width() > 799 ? 85 : 0} unmountIfInvisible={true}>
                  <div className="work__left-button">
                    <a href={`http://yova.praid.com.ua/${area}pdf/${id}`}>
                      <ButtonDecorate 
                        title="media kit" 
                        title_ua="Медіа комплект"
                        id={'buttonMedia'} 
                        autoStart={true}
                      />
                    </a>
                  </div>
                </LazyLoad>
              </div>
              <div className="work__right">
                <h1 itemProp="name" className="work__right-title">
                  {language === 'en' ? currentWorkData[0].title : currentWorkData[1].title}
                </h1>
                {
                  currentWorkData[language === 'en' ? 0 : 1].description.split('\n').map((item, index) => {
                    if (index >= 3) {
                      return 
                    } 
                    return <p itemProp="about" key={item} className="work__right-text">
                      {item}
                    </p>
                  })
                }
              </div>
            </animated.div>
            <button className="work__details" onClick={() => setShowDetails(!showDetails)}>MORE DETAILS {`${showDetails === true ? '-' : '+'}`}</button>
            <WorkPageGallery 
              images={currentAllImages} 
              text_en={currentWorkData[0].description.split('\n')} 
              text_ua={currentWorkData[1].description.split('\n')}
            />
          <h3 className='work__also'>{language === 'en' ? 'YOU MIGHT ALSO LIKE' : 'Вам може сподобатись'}</h3>
          <MassonryGallery 
            title={false} 
            button={false} 
            worksArr={worksForAlsoLike(works)} 
            count={4} 
            area='works' 
            photoLoadButton={true}
            buttonAutoStart={true}
          />
        </div>
        <JSONLD>
          <Generic type="CreativeWork" jsonldtype="CreativeWork" 
            schema={{ 
              contentLocation: `${currentWorkData[0].location}`,
              materialExtent: `${currentWorkData[0].area}`, 
              creativeWorkStatus: `${currentWorkData[0].status}`, 
              genre: `${currentWorkData[0].art_pattern}`, 
              locationCreated: `${currentWorkData[0].studio}`, 
              additionalType: `${currentWorkData[0].function}`,
            }}>
            <Generic type="author" jsonldtype="Person" schema={{name: `${currentWorkData[0].designer}`}}/>
            <Generic type="contributor" jsonldtype="Person" schema={{ name: `${currentWorkData[0].photographer}` }}/>
          </Generic>
        </JSONLD>
        {/* <Helmet>
          <meta name="description" content="Helmet application" />
          <script  type="application/ld+json">
            {`{
              "@context":"https://schema.org/","@type":"CreativeWork","contentLocation":"Antonovicha st., 1, Kiev, Ukraine","materialExtent":"12 m2","creativeWorkStatus":"Realization 2018","genre":"Verdi","locationCreated":"YOVA YAGER hospitality design","additionalType":"Mobile van","author":{"@type":"Person","name":"Yova Yager"},"contributor":{"@type":"Person","name":"Sergei Polushko"}
            }`}
          </script>
        </Helmet> */}
      </section>
    );
  }
}
 
export default WorkPage;